.taskArea {
    height: 36px;
    font-Size: 14px;
}

.taskItem {
    padding: 0px 15px;
    height: 100%;
    align-items: center;
}

.taskItem .taskNames {
    padding-left: 6px;
}

.taskItem .numberArea {
    display: flex;
    justify-content: flex-end; 
}

.numberArea .numberBox {
    width: 23px;
    height: 14px;
    margin-left: auto;
    text-align: center;
}

.numberBox .taskNumbers{
    width: 100%;
    height: 100%;
    font-Size: 10px;
}

.Menu {
    padding: 15px;
    min-width: 290px;
    max-width: 290px;
    min-height: 674px;
    height: calc(100vh - 30px);
    background-color: #E8E8E8;
    box-sizing: border-box;
    border-radius: 23px;
    display: flex;
    flex-direction: column;
}

#Searchbar {
    background-color: #E8E8E8;
    border: 1px #EDEDED solid;
    font-size: 14px;
}

.Menu .menuGrid {
    margin: 0px;
}

.menuGrid .menuHeader {
    font-weight: 400;
    font-size: 24px;
    margin-top: 10px;
}

.menuGrid .hamberger {
    display: flex;
    justify-Content: flex-end;
    margin-Top: 10px;
}

.menuGrid .searchGrid {
    height: 36px;
    margin-Top: 16px;
    margin-Bottom: 26px;
}

.searchGrid .searchBox {
    height: 36px;
    border: 2px #D5D5D5 solid;
    background-color: #E8E8E8;
    color: #767676;
    display: flex;
    align-Items: center;
    padding: 0 10px; /*Optional: Add some padding around the icon and text*/
}

.Title {
    font-size: 10px;
    font-weight: bold;
    margin-bottom: 14px;
    width: 100%;
}

.menuGrid .taskMenuMargin {
    margin: 1px 0px;
}

.menuGrid .listMenu {
    height: 34%;
    overflow: auto;
}

.menuGrid .addListArea {
    margin-top: 7px;
    height: 36px;
    font-Size: 14px;
    background-Color: #E8E8E8;
}

.addListArea .addListGrid {
    padding: 0px 15px;
    height: 100%;
}

.listScrollArea {
    overflow: auto;
    max-height: 170px;
}

.listScrollArea::-webkit-scrollbar {
    width: 8px;
}

.listScrollArea::-webkit-scrollbar-thumb {
    background-color: #767676;
    border-radius: 4px;
}
  
.listScrollArea::-webkit-scrollbar-thumb:hover {
    background-color: #555;
}

.Menu .signOut {
    margin-top: auto;   
    margin-bottom: 19px;
}